import React, {Component} from "react";
import {MuiPickersUtilsProvider} from '@material-ui/pickers';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {Redirect, Route, Switch} from "react-router-dom";
import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";

export default class MultiStepForm extends Component {
    state = {
        // step1
        userInfo: {},
        currentAddress: {},
        lat_long: '',
        moveType: '',
        newAddress: {},
        ltLngNew: '',
        cardDetails: {}

    };


    handleChangeField = (e = {}, name, value) => {
        this.setState(
            {
                [name]: value,
            },
            () => console.log(this.state)
        );
    };

    handleChangeState = newState => {
        this.setState(newState);
    }

    submitData() {
        const data = {
            userInfo: this.state.userInfo,
            currentAddress: this.state.currentAddress,
            moveType: this.state.moveType,
            newAddress: this.state.newAddress,
            cardDetails: this.state.cardDetails
        };


        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = 'https://migrationade.com/api/changeaddress';

        if((Object.keys(this.state.userInfo).length > 0) && (Object.keys(this.state.currentAddress).length > 0) &&
           (Object.keys(this.state.moveType).length > 0) && (Object.keys(this.state.newAddress).length > 0) &&
           (Object.keys(this.state.cardDetails).length > 0)){
            axios.post((proxyurl+url), data,{
                headers: {
                    'Content-Type' : 'application/json',
                    'Authorization' : 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.IjVmN2IzMzg4NmQ3ZTVkYTJkYjM4YzRkNmUyOGEyOTE4YzlmNzFiOWQ1OGNkYmExOGJmNzg2ZWZiNzZhM2Q1NmVlNjlhM2IzNDM5NTJhNWY3YjMzODg2ZDdlYzE1YjU2MDkyYTM4OGY4YjE4MDEzMzlmZDc5YWIwM2U4NWY3YjMzODg2ZDgxYiI.daQuucAv0MYKUyjGNlprGj6he6SXhnEqbROFE6eIvmw',
                    'Access-Control-Allow-Credentials': 'true'
                }
            })
                .then((response) => {
                    if(response.status === 200){
                        console.log('successful');
                    }
                })
                .catch((error)=> console.log('Error::',error))
                .finally(() => console.log("Data Sent", {data: this.state}));
        }
    }

    renderStep = (props) => {
        const {history} = props;
        const {path, params} = props.match;
        const currentPath = `/${path.split("/")[1]}`;
        if (params.step === "thankyou") {
            this.submitData();
            return <ThankYou history={history} nextStep={`${currentPath}/1`} />;
        } else if (params.step === "decline") {
            return <Decline/>;
        }
        const step = parseInt(params.step);
        
        switch (step) {
            case 1:
                return (
                    <Step1
                        history={history}
                        nextStep={`${currentPath}/2`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 2:
                return (
                    <Step2
                        history={history}
                        prevStep={`${currentPath}/1`}
                        nextStep={`${currentPath}/3`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 3:
                return (
                    <Step3
                        history={history}
                        prevStep={`${currentPath}/2`}
                        nextStep={`${currentPath}/4`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 4:
                return (
                    <Step4
                        history={history}
                        prevStep={`${currentPath}/3`}
                        nextStep={`${currentPath}/5`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 5:
                return (
                    <Step5
                        history={history}
                        prevStep={`${currentPath}/4`}
                        nextStep={`${currentPath}/6`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 6:
                return (
                    <Step6
                        history={history}
                        prevStep={`${currentPath}/5`}
                        nextStep={`${currentPath}/7`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 7:
                return (
                    <Step7
                        history={history}
                        prevStep={`${currentPath}/6`}
                        nextStep={`${currentPath}/8`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 8:
                return (
                    <Step8
                        history={history}
                        prevStep={`${currentPath}/7`}
                        nextStep={`${currentPath}/9`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 9:
                return (
                    <Step9
                        history={history}
                        prevStep={`${currentPath}/8`}
                        nextStep={`${currentPath}/thankyou`}
                        state={this.state}
                        onChangeField={this.handleChangeField}
                    />
                );
            default:
                return <div>invalid step</div>;
        }
    };

    render() {
        const {path} = this.props.match;
        return (
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <Switch>
                    <Route
                        path="/start"
                        exact
                        render={() => <Redirect to={`${path}/1`}/>}
                    />
                    <Route path={`${path}/:step`} render={(props) => <>
                        {this.renderStep(props)}
                        <div style={{position: 'fixed', bottom: '0px', padding: 10, width: '100%'}}>
                            <small style={{display: 'flex', justifyContent: 'center'}}>SBA
                                Disasters Pros is in no way shape or form affiliated with the U.S. Government or the
                                Small Business
                                Administration</small>
                        </div>
                    </>}/>
                </Switch>
            </MuiPickersUtilsProvider>
        );
    }
}
